import debounce from 'lodash/debounce';

export default class Drop {
    constructor(container) { // css selector representing dropzone id
        this.container = document.querySelector(container)
        // this.domElem = Object // Dragged elem .content (html string) property converted to dom element with DomParser()
        this.debouncedHandleDrag = debounce(this.handleDragOver, 300, { maxWait: 500 })
        this.bindEvents()
    }
    bindEvents() {
        this.container.addEventListener('dragenter', event => {
            console.log('dragenter')
            console.log(this.draggedElem)
            console.log(this.draggedEvent)
            console.log(event.target)
        })
        this.container.addEventListener('dragover', (event) => {
            event.preventDefault()
            this.debouncedHandleDrag()
        })
        this.container.addEventListener('drop', event => {
            console.log('drop')
            // this.append(this.draggedElem)
        })
    }
    handleDragOver() {
        console.log('handleDragOver')
    }
}
